nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
  opacity: 0.2;
  transition: var(--transition);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

nav a.active {
  background: var(--color-bg);
  color: var(--color-white);
}

nav:hover {
  opacity: 1;
}

/* media queries(medium) */

@media screen and (max-width: 1024px) {
  nav {
    opacity: 1;
  }
  nav a {
    padding: 1.1rem;
    font-size: 2rem;
  }
}

/* media queries(small) */

@media screen and (max-width: 600px) {
  nav {
    opacity: 1;
  }
}
