header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/*  CTA  */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* Header Socials */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/*  image */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 32rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 3rem;
  border-radius: 12rem 12rem 0 0;
}

/* scroll down */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* media queries(medium) */

@media screen and (max-width: 1024px) {
  header {
    height: 100rem;
    padding-top: 15rem;
    overflow: hidden;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
  .me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 36rem;
    height: 62rem;
    position: absolute;
    left: calc(50% - 18rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0;
  }
  .initial h5 {
    font-size: 2rem;
  }
  .initial h1 {
    font-size: 5rem;
  }
}

/* media queries(small) */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}
